  color: #3e65da;

  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

a:hover,
a:active {
  color: #3e65da;
  text-decoration: none;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

a:focus,
