/* Video Play button */
.playButton {
  position: relative;
  width: 74px;
  height: 74px;
  line-height: 74px;
  text-align: center;
  border-radius: 50%;
  display: inline-block;
  font-size: 18px;
  -webkit-box-shadow: 0px 0px 50px -35px rgba(0, 0, 0, 0.4);
  box-shadow: 0px 0px 50px -35px rgba(0, 0, 0, 0.4);
  -webkit-transition: all 0.8s ease-in-out;
  transition: all 0.8s ease-in-out;
}

.playButton:before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  bottom: 50%;
  right: 50%;
  -webkit-transform: translate(50%, 50%) scale(1.3);
  transform: translate(50%, 50%) scale(1.3);
  border-radius: 50%;
  background-color: #fff;
  opacity: 0.2;
}

.playButton.playButton-pulsing:before {
  opacity: 0.6;
  -webkit-transform: translate(50%, 50%) scale(1);
  transform: translate(50%, 50%) scale(1);
  -webkit-animation: pulsing-ring 1800ms cubic-bezier(0.2, 0.6, 0.35, 1)
    infinite;
  animation: pulsing-ring 1800ms cubic-bezier(0.2, 0.6, 0.35, 1) infinite;
}

@-webkit-keyframes pulsing-ring {
  100% {
    -webkit-transform: translate(50%, 50%) scale(1.5);
    transform: translate(50%, 50%) scale(1.5);
    opacity: 0;
  }
}

@keyframes pulsing-ring {
  100% {
    -webkit-transform: translate(50%, 50%) scale(1.5);
    transform: translate(50%, 50%) scale(1.5);
    opacity: 0;
  }
}

