/* Brands Grid */
.brands-grid {
  overflow: hidden;
}

.brands-grid > .row > * {
  padding-top: 20px;
  padding-bottom: 20px;
  position: relative;
}

.brands-grid.separator-border > .row > *:after,
.brands-grid.separator-border > .row > *:before {
  content: "";
  position: absolute;
}

.brands-grid.separator-border > .row > *:after {
  width: 100%;
  height: 0;
  top: auto;
  left: 0;
  bottom: -1px;
  border-bottom: 1px dotted currentColor;
  opacity: 0.3;
}

.brands-grid.separator-border > .row > *:before {
  height: 100%;
  top: 0;
  left: -1px;
  border-left: 1px dotted currentColor;
  opacity: 0.3;
}

.brands-grid.separator-border.separator-border-light > .row > *:after {
  border-bottom: 1px dotted rgba(250, 250, 250, 0.15);
}

.brands-grid.separator-border.separator-border-light > .row > *:before {
  border-left: 1px dotted rgba(250, 250, 250, 0.15);
}

/* Banner */
.banner .item {
  position: relative;
}

.banner .item img {
  vertical-align: middle;
}

.banner .caption {
  position: absolute;
  z-index: 2;
  bottom: 0;
  width: 100%;
  padding: 15px 20px;
}

.banner .caption h2,
.banner .caption h3 {
  font-size: 19px;
  color: #fff;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.banner .caption p {
  color: rgba(250, 250, 250, 0.8);
  margin-bottom: 0px;
}

.banner .rounded .banner-mask,
.banner .rounded img {
  border-radius: 0.25rem;
}

.banner .banner-mask {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
  backface-visibility: hidden;
  background: -webkit-linear-gradient(
    top,
    rgba(0, 0, 0, 0.01),
    rgba(0, 0, 0, 0.6)
  );
  background: -moz-linear-gradient(
    top,
    rgba(0, 0, 0, 0.01),
    rgba(0, 0, 0, 0.6)
  );
  background: -o-linear-gradient(top, rgba(0, 0, 0, 0.01), black);
  background: -ms-linear-gradient(top, rgba(0, 0, 0, 0.01), black);
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.01), black);
  opacity: 0.7;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.banner .item:hover .banner-mask {
  opacity: 0.95;
  filter: alpha(opacity=95);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.banner.style-2 .caption {
  background: rgba(0, 0, 0, 0.5);
}

.banner.style-2 .banner-mask {
  opacity: 0;
}

.banner.style-2 .item:hover .banner-mask {
  opacity: 0.4;
}

.banner.style-2 .item:hover .caption h2 {
  margin-left: 8px;
}

/* Simple ul */
.simple-ul > li {
  position: relative;
  list-style-type: none;
  line-height: 24px;
}

.simple-ul > li:after {
  content: " ";
  position: absolute;
  top: 12px;
  left: -15px;
  border-color: #000;
  border-top: 1px solid;
  border-right: 1px solid;
  width: 6px;
  height: 6px;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
  transform: translate(-50%, -50%) rotate(45deg);
}

.rtl .simple-ul > li:after {
  right: -15px;
  left: auto;
  border-color: #000;
  border-top: 0px;
  border-right: 0px;
  border-left: 1px solid;
  border-bottom: 1px solid;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
  transform: translate(-50%, -50%) rotate(45deg);
}

/* Steps Progress bar */
.widget-steps > .step {
  padding: 0;
  position: relative;
}

.widget-steps > .step .step-name {
  font-size: 16px;
  margin-bottom: 5px;
  text-align: center;
}

.widget-steps > .step > .step-dot {
  position: absolute;
  width: 30px;
  height: 30px;
  display: block;
  background: #fff;
  border: 1px solid #28a745;
  top: 45px;
  left: 50%;
  margin-top: -15px;
  margin-left: -15px;
  border-radius: 50%;
}

.widget-steps > .step > .step-dot:after {
  width: 10px;
  height: 10px;
  border-radius: 50px;
  position: absolute;
  top: 9px;
  left: 9px;
}

.widget-steps > .step.complete > .step-dot {
  background: #28a745;
}

.widget-steps > .step.complete > .step-dot:after {
  content: "\f00c";
  font-weight: 900;
  color: #fff;
  font-family: "Font Awesome 5 Free";
  top: 3px;
  left: 7px;
}

.widget-steps > .step.active > .step-dot:after {
  background: #28a745;
  content: "";
}

.widget-steps > .step > .progress {
  position: relative;
  background: #bbb;
  border-radius: 0px;
  height: 1px;
  box-shadow: none;
  margin: 22px 0;
}

.widget-steps > .step > .progress > .progress-bar {
  width: 0px;
  box-shadow: none;
  background: #28a745;
}

.widget-steps > .step.complete > .progress > .progress-bar {
  width: 100%;
}

.widget-steps > .step.active > .progress > .progress-bar {
  width: 50%;
}

.widget-steps > .step:first-child.active > .progress > .progress-bar {
  width: 0%;
}

.widget-steps > .step:last-child.active > .progress > .progress-bar {
  width: 100%;
}

.widget-steps > .step.disabled > .step-dot {
  border-color: #bbb;
}

.widget-steps > .step:first-child > .progress {
  left: 50%;
  width: 50%;
}

.widget-steps > .step:last-child > .progress {
  width: 50%;
}

.widget-steps > .step.disabled a.step-dot {
  pointer-events: none;
}

@media (max-width: 575.98px) {
  .widget-steps > .step .step-name {
    font-size: 14px;
  }
}

.rtl .widget-steps > .step > .step-dot {
  top: 45px;
  right: 50%;
  left: auto;
  margin-left: 0;
  margin-right: -15px;
}

.rtl .widget-steps > .step > .step-dot:after {
  left: 0px;
  right: 9px;
}

.rtl .widget-steps > .step.complete > .step-dot:after {
  left: 0px;
  right: 7px;
}

.rtl .widget-steps > .step:first-child > .progress {
  left: auto;
  right: 50%;
}

